    ar: 'ينظرالمحامي باشي بقلب متعاطف وعقل واعي الى المعاناة الكبيرة التي يمر بها المهاجرون في رحلة قدومهم الى الولايات المتحدة ويقدر تماما أهمية حصول المهاجرين على صفة ووثائق قانونية تمكنهم من العيش في الولايات المتحدة الأمريكية كأي مواطن أمريكي. ولذالك فقد عقد المحامي باشي العزم على مد يد العون والمساعدة وتوفير التمثيل القانوني الأفضل لكل المهاجرين وعائلاتهم في عالم الهجرة المتغير باستمرار. اتخذ المحامي باشي مهنة المحاماة بسبب شغفه الكبير في مساعدة المهاجرين كواجب إنساني أولاً و للحصول على وضع قانوني سليم في الولايات المتحدة ثانيا كخطوة أولى وأساسية على طريق الحلم الأمريكي. حاليا يتولى المحامي باشي قضايا الهجرة في مختلف الولايات الامريكية كما انه يتولى القضايا المتعلقة بالمخالفات المرورية وقضايا الجنح داخل ولاية كارولاينا الشمالية.',
